// PRELOADER
.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 101;
    transition: opacity 1s ease-in-out;


    img {
        width: 200px;
    }
    .percent {
        -webkit-text-stroke: 1px #088ECB;
        font-weight: 700;
        font-size: 200px;
        color: transparent;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
.preloader.done {
    opacity: 0;
}
