// MOBILE MENU
.mobile-menu {
    display: flex;
    justify-content: center;
    align-items: center;

    &__list {
        li {
            text-align: center;
            &:not(:last-child) {
                margin-bottom: 16px;
            }
        }
        a {
            
            color: #fff;
            font-size: 24px;
            text-decoration: none;
        }
