// PROMO SECTION
.promo-section {
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        background: #010101;
        opacity: 0;
    }
}
