// BEAMS SECTION
.beams-section {
    background: url('../img/noise.png') fixed;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        background: #010101;
        opacity: 0.99;
    }

    .beams {
        z-index: 2;
    }
    .tables {
        width: 100%;
        margin: calc(100vh - 456px) 0 100px 0; // 356
        transform: translateX(120%);
        z-index: 2;

        @media(min-width: 1920px) {
            margin: calc(100vh - 856px) 0 500px 0; // 356
        }
    }
    .table-1 {
        transform-origin: 50% 100%;
    }
    .parallax-swimmer {
        z-index: 2;
    }
    .drops {
        z-index: 1;
    }
    .tables-wrapper {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        position: relative;

        img {
            width: 100%;
        }
        .table-con {
            position: relative;
            flex: 0 0 calc(100% / 3);
            display: flex;
            justify-content: center;
        }
        .second-con {
            max-width: 100%;
            flex: 0 0 calc(100% / 3);
        }
        .third-con {
            right: calc((-100% / 3) / 2);
        }
        .first-con {
            right: calc((100% / 3) / 2);
        }
    }
}
