            }
        }
        .form-wrapper {
            flex: 0 0 50%;

            .form {
                display: flex;
                flex-direction: column;

                &-input {
                    width: 100%;
                    font-size: 64px;
                    font-weight: 400;
                    color: #fff;
                    margin: 0 0 40px 0;
                    border: none;
                    border-bottom: 1px solid #fff;
                    background: none;
                    outline: none;
                    padding-bottom: 4px;

                    &::placeholder {
                        -webkit-text-stroke: 1px #fff;
                        color: transparent
                    }
                }
                &-button {
                    width: fit-content;
                    border: none;
                    background: none;
                    text-align: left;
                    font-size: 64px;
                    font-weight: 400;
                    color: #fff;
                    position: relative;
                    cursor: pointer;

                    &:after {
                        content: '';
                        width: 72px;
                        height: 64px;
                        background: url('../img/icons/send-arrow.svg') center center no-repeat;
                        position: absolute;
                        top: 50%;
                        left: calc(100% + 24px);
                        transform: translate(0, -50%);
                    }
                }
            }
        }
    }
}
