// LOCATION SIDE
.location-section {
    padding-bottom: 420px;

    .section-head {
        padding-top: 220px;
    }
    &__body {
        display: flex;
        justify-content: space-between;
        .side-1, .side-2 {
            .image-wrapper {
                position: relative;
                margin-bottom: 40px;
                img {
                    width: 100%;
                    clip-path: circle(50%);
                }
                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: #548BCB;
                    z-index: 2;
                    mix-blend-mode: color;
                    border-radius: 100%;
                }
            }
            .text-content {
                p:first-child {
                    font-size: 32px;
                    line-height: calc(48 / 32 * 100%);
                    font-family: 'Formular', sans-serif;
                    font-weight: 500;
                }
                p {
                    text-align: center;
                    font-family: 'Inter', sans-serif;
                    color: #fff;
                    font-size: 20px;
                    line-height: calc(32 / 20 * 100%);
                    font-weight: 400;
                    margin-bottom: 48px;
                }
            }
        }
        .side-1 {
            flex: 0 0 49%;
            margin-right: 3%;
            display: flex;
            justify-content: center;
            align-items: flex-start;

            .location-cart {
                .image-wrapper {
                    transform: scale(0.7);
                }
            }
        }
        .side-2 {
            flex: 0 0 48%;

            .location-cart {
                margin-top: 430px;
            }
        }
    }
}
