// GALLERY BLOCK
.gallery-block {
    margin: 220px 0;
    overflow: hidden;
    &__wrapper {
        position: relative;
    }
    &__letters {
    }
    &__image {
        position: absolute;
        top: 0;
        left: 0;
    }
    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background: #088ECB;
        z-index: -1;
        opacity: 0;
    }
    .fixed-trigger {
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background: transparent;
        z-index: -1;
        opacity: 0;
    }
}

.letters-block {
    height: 100vh;
    &__string {
        position: relative;
        height: 25vh;
        
        &_left {

            .line-black {
                position: absolute;
                left: 0;
                top: 100%;
                height: 3px;
                width: 100%;
                background: #010101;
                z-index: 2;
            }
            .line {
                position: absolute;
                left: 0;
                top: 100%;
                height: 1px;
                width: 100%;
                background: #fff;
                z-index: 1;
            }
        }
        &_right {
            transform: translateX(100%);

            .line {
                position: absolute;
                transform: translateX(-200%);
                top: 100%;
                height: 1px;
                width: 100%;
                background: #fff;
            }
        }
        
        .string-line {
            -webkit-text-stroke: 1px #fff;
            color: transparent;
            font-weight: 900;
            font-style: italic;
            font-size: 160px;
            line-height: 25vh;
            text-transform: uppercase;
            white-space: nowrap;
        }
    }
}

.image-block {
    position: relative;
    z-index: 3;
    & > div {
        height: calc(50vh - 80px);
        display: flex;
        flex-wrap: nowrap;
    }
    &__top {  
        padding-left: 100vw;  
        margin-top: 80px;
    }
    &__bottom {
        position: relative;
        padding-left: 150vw;
        margin-bottom: 80px;
    }

    &__item {
        height: 100%;
        width: 100vw;
        overflow: hidden;
        display: flex;
        align-items: center;

        img {
            max-height: 100%;
        }
    }

    .last-image {
        position: absolute;
        left: 100%;
        top: -50%;
    }
}
