// BUBBLES BLOCK
.bubbles-block {
    margin: 220px 0;
    min-height: 100vh;
    &__wrapper {
        position: relative;
        height: 100%;
    }
    &__swim-logo {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        img {
            transform: scale(0);
        }
    }
    &__swim-bubbles {
        width: 100%;
        position: absolute;
        //top: 100%;
        opacity: 0;
    }
    &__string-line {
        position: absolute;
        top: 50%;
        transform: translate3d(100%, -50%, 0);
        mix-blend-mode: difference;
        span {
            color: #fff;
            font-weight: 900;
            font-style: italic;
            font-size: 160px;
            line-height: 192 / 160 * 100%;
            text-transform: uppercase;
            white-space: nowrap;
        }
    }
}
